import { LitElement, html } from "lit";
import { styles } from "./abmmodal-style";

class ModalClass extends LitElement {
  static styles = [styles];

  static get properties() {
    return {
      open: { type: Boolean },
      message: { type: String },
    };
  }

  constructor() {
    super();
    this.open = true;
    this.message = "";
  }

  
  hideModal() {
    this.open = false; 
  }

  render() {
    return html`
      <div class="principal-div" style="display: ${this.open ? 'flex' : 'none'};">
      <div class="modal" role="dialog" aria-modal="true" aria-labelledby="modal-title">
          <p id="modal-title" class='title'>${this.message}</p>
          <button class="buttom" @click="${this.hideModal}">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="16"
              height="16"
              fill="currentColor"
              class="bi bi-x"
              viewBox="0 0 16 16"
            >
              <path
                d="M4.646 4.646a.5.5 0 0 1 .708 0L8 7.293l2.646-2.647a.5.5 0 0 1 .708.708L8.707 8l2.647 2.646a.5.5 0 0 1-.708.708L8 8.707l-2.646 2.647a.5.5 0 0 1-.708-.708L7.293 8 4.646 5.354a.5.5 0 0 1 0-.708"
              />
            </svg>
          </button>
        </div>
      </div>
    `;
  }
}

customElements.define("abm-modal", ModalClass);
